.page-contacts {
	display: flex;

	@media (max-width: 690px) {
		flex-direction: column;
	}
	&__item {
		margin-bottom: $gutter*2;
	}
	&__link {
		color: $dark;
		line-height: 1.5;
		&:hover {
			text-decoration: underline;
		}
	}
	&__title {
		font-size: 1.1rem;
		font-weight: 600;
		color: $dark;
		margin-bottom: $gutter/2;
	}
	&__subtitle {
		font-size: 1rem;
		font-weight: 500;
		color: $dark;
		margin: .625rem 0;
	}
	&__soc {
		display: flex;
		align-items: center;
		margin-bottom: $gutter/2;
	}
	&__icon {
		width: 1rem;
		height: 1rem;
		fill: $dark;
		margin-right: 5px;
	}
	&__form {
		@extend .block-brands__form;
		margin-left: 75px;

		@media (max-width: 690px) {
			margin-left: 0;
		}
	}
}